import React, { Component } from 'react';
import '@/assets/css/watchQuestions.css';
import { Select, Button } from 'antd';
import axios from 'axios';
import QuestionList from '@/components/Watchquestion/index';
import {
	examSubject,
	examType,
	TopicTypes,
	examWatch,
	search
} from '@/api/watch';

const { Option } = Select;

class watchQuestions extends Component {
	state = {
		lesson: '',
		exam: '',
		question: '',
		dataList: '', //
		subjectData: '', //
		examType: '', // 
		TopicTypes: '', //
		all: '',
		flag: false
	};
	componentDidMount() {
		// js
		examSubject().then(res => {
			this.setState({
				subjectData: res.data.result
			});
		});

		//
		examWatch().then(res => {
			this.setState({
				dataList: res.data.result
			});
		});

		// 
		examType().then(res => {
			this.setState({
				examType: res.data.result
			});
		});

		//
		TopicTypes().then(res => {
			this.setState({
				TopicTypes: res.data.result
			});
		});

		//
		axios.get('/exam_manage/exam_watch').then(res => {
			this.setState({
				dataList: res.data.result
			});
		});
	}

	//all
	allLessonTypeFn() {
		let subject_text = this.state.subjectData.map((item, index) => {
			return item.subject_text;
		});
		this.setState({
			lesson: subject_text,
			flag: true
		});
	}

	//
	lessonTypeFn(item) {
		this.setState({
			lesson: item,
			flag: false
		});
	}
	//
	handleChangeExam(value) {
		this.setState({
			exam: value
		});
	}
	//
	handleChangeQuestion(value) {
		this.setState({
			question: value
		});
	}

	//
	searchFn() {
		let { lesson, exam, question, flag } = this.state;
		if (flag) {
			examWatch().then(res => {
				this.setState({
					dataList: res.data.result
				});
			});
		} else {
			search({
				subject_text: lesson,
				exam_type: exam,
				questions_type_text: question
			}).then(res => {
				this.setState({
					dataList: res.data.result
				});
			});
		}
	}

	render() {
		let { dataList, subjectData, examType, TopicTypes } = this.state;
		return (
			<div className='watch'>
				<div
					className='top'
					style={{
						padding: 24,
						background: '#fff',
						minHeight: 180,
						borderRadius: '10px'
					}}>
					<ul>
						<li>
							 
							<Button onClick={this.allLessonTypeFn.bind(this)}>
								all
							</Button>
							{subjectData &&
								subjectData.map((item, index) => {
									return (
										<Button
											key={index}
											onClick={this.lessonTypeFn.bind(
												this,
												item.subject_text
											)}>
											{item.subject_text}
										</Button>
									);
								})}
						</li>
						<li className='li-two'>
							<div className='exam-type'>
								<span>:</span>
								<Select
									defaultValue=''
									style={{ width: 200 }}
									onChange={this.handleChangeExam.bind(this)}>
									{examType &&
										examType.map(item => {
											return (
												<Option
													value={item.type_name}
													key={item.tid}>
													{item.type_name}
												</Option>
											);
										})}
								</Select>
							</div>

							<div className='subject-type'>
								<span>:</span>
								<Select
									defaultValue=''
									style={{ width: 200 }}
									onChange={this.handleChangeQuestion.bind(
										this
									)}>
									{TopicTypes &&
										TopicTypes.map((item, index) => {
											return (
												<Option
													value={
														item.questions_type_text
													}
													key={index}>
													{item.questions_type_text}
												</Option>
											);
										})}
								</Select>
							</div>
							<div className='searchBut'>
								<Button
									type='primary'
									icon='search'
									onClick={this.searchFn.bind(this)}>
									
								</Button>
							</div>
						</li>
					</ul>
				</div>

				<div
					style={{
						padding: 24,
						background: '#fff',
						minHeight: 360,
						marginTop: '20px',
						borderRadius: '10px'
					}}>
					{dataList &&
						dataList.map((item, index) => {
							return (
								<QuestionList
									key={index}
									item={item}
									props={this.props}></QuestionList>
							);
						})}
				</div>
			</div>
		);
	}
	componentWillUnmount() {
		this.setState = (state, callback) => {
			return;
		};
	}
}

export default watchQuestions;
